@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
* {
  box-sizing: border-box;
}
body {
  margin: 0px;
  padding: 0px;
  color: white; 
  font-family: roboto;
  font-size: 1.5rem;
}
.App {
  height: 100vh;
  display: grid;
  grid-template: 33% 33% 33% / 20% 80%;
  grid-template-areas: 
  "places degrees"
  "places hours"
  "places days";
  background: #88f;
}
.Places {
  grid-area: places;
  background: #77e;
}
.Degrees {
  grid-area: degrees;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  text-align: center;
}
.Hours {
  grid-area: hours;
  background: #77e;
  margin: 20px;
  height: 200px;
  border-radius: 10px;
  display: flex;
  overflow-x: scroll;
}
.Days {
  grid-area: days;
  margin: 20px;
  border-radius: 10px;
  display: flex;
  overflow-x: scroll;
} 