.searchInput {
  width: 80%;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.searchSubmit {
  width: 20%; 
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
input {
  border: none;
  padding: 10px;
  transition: .3s ease-in-out;
}
input:focus {
  outline: none;
}
.searchList {
  position: absolute;
  background: #88f;
  width: 100%;
  top: 12px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  opacity: 0;
  transition: .3s ease-in-out;
  padding: 5px;
}
li {
  display: block;
  border-bottom: 1px solid #fff;
}
.search {
  position: relative;
}
.places {
  padding: 15px;
}
.active {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.activeList {
  opacity: 1;
}